.profile-list .euiCard {
  display: flex;
  flex-direction: row !important;
  align-items: center !important;
}
.profile-list .euiCard__top{
  max-width: 150px !important;
}
.profile-list .euiCard__top + .euiCard__content{
  margin-top: 0px !important;
}

.profile-list .euiCard__top .euiCard__image{
  width: 150px !important;
  /* height: 150px; */
  border-radius: 50%;
  padding-right: 15px;
  left: unset;
  top: unset;
  margin-bottom: unset;
  /* -webkit-filter: blur(20px); */
}
.profile-list .euiFlexItem{
  position: relative;
}

.profile-list .euiCard__footer{
  max-width: 100px;
  position: absolute;
  right: 0px;
  top: 0px;
}

.profile-list .euiCard__description p {
  display: inline-block;
  max-width: 700px;
  overflow-x: hidden;
  white-space: nowrap;
  width: 100%;
  text-overflow: ellipsis;
}

.euiHeaderSectionItem{
  display: flex;
  align-items: center;
}

.euiText * {
  font-weight: normal;
}

input {
  max-width: 60px;
    font-weight: bold;
    border-radius: 5px;
    padding: 2px 5px;
    border: 1px solid #aaa;
}

* {
  outline: none;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 200px;
  padding: 15px;
}